import VueRouter from 'vue-router'
import Vue from 'vue'
import Login from '../views/Login'
import OverView from '../views/OverView'

Vue.use(VueRouter)
const routes = [
    {
        path:'/',
        redirect: "/overview", //设置默认指向的路径
    },
    {
        path:'/login',
        component:Login
    },
    {
        path:'/overview',
        component:OverView
    },
]
const router = new VueRouter({
    routes:routes
})

export default router