<template>
<div class="container">
    <div class="header">
        <span>泵组：</span>
        <el-select v-model="pumpNo" placeholder="请选择" @change="selectionChange">
            <el-option
            v-for="item in 6"
            :key="item"
            :label="item+'号'"
            :value="item" />
        </el-select>
        <span style="margin-left:50px">历史记录：</span>
        <el-date-picker
        v-model="timeRange"
        @change="selectionChange"
        type="datetimerange"
        range-separator="至"
        start-placeholder="开始时间"
        end-placeholder="结束时间" />
        <el-button type="primary" style="margin-left:10px" @click="selectionChange">查 询</el-button>
    </div>
    <el-tabs type="border-card" tab-position="left" style="height:600px">
        <el-tab-pane label="状态">
                <div style="width:100%;display:flex;flex-wrap:wrap">
                    <div id="head_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                    <div id="work_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                    <div id="efficiency_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                    <div id="predict_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                </div>
        </el-tab-pane>
        <el-tab-pane label="优化">
                <div style="width:100%;display:flex;flex-wrap:wrap">
                    <!-- <div id="head_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                    <div id="work_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                    <div id="efficiency_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div>
                    <div id="predict_flow" style="width:calc(50% - 40px);height:250px;margin:20px"></div> -->
                </div>
        </el-tab-pane>
    </el-tabs>
</div>
</template>

<script>
Date.prototype.Format =  function (fmt) {  // author: meizz 
     var o = {
        "M+":  this.getMonth() + 1,  // 月份 
        "d+":  this.getDate(),  // 日 
        "h+":  this.getHours(),  // 小时 
        "m+":  this.getMinutes(),  // 分 
        "s+":  this.getSeconds(),  // 秒 
        "q+": Math.floor(( this.getMonth() + 3) / 3),  // 季度 
        "S":  this.getMilliseconds()  // 毫秒 
    };
     if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, ( this.getFullYear() + "").substr(4 - RegExp.$1.length));
     for ( var k  in o)
     if ( new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
     return fmt;
}

const legendFormatter=(name)=>{
    if(name==='head')return '扬程'
    if(name==='flow')return '流量'
    if(name==='work')return '功率'
    if(name==='efficiency')return '效率'
    if(name==='predict')return '预警'
}
import api from '../api/api'
import * as echarts from 'echarts'
export default {
  name: 'LoginView',
  props: {
    msg: String,
  },
  data(){
    return{
        pumpNo:1,
        timeRange:[]
    }
  },
  mounted(){
    this.selectionChange();
    setInterval(this.selectionChange, 60000);
  },
  methods:{
    selectionChange(){
        console.log(this.pumpNo)
        console.log(this.timeRange)
        this.getWorkFlow();
        this.getHeadFlow();
        this.getEfficiencyFlow();
        this.getPredictFlow();
    },
    getHeadFlow(){
        /* pumpNo=1&start=2022-10-31 07:00:00&end=2022-10-31 07:16:59 */
        let mydata={}
            mydata.pumpNo=this.pumpNo;
        if(this.timeRange.length!=0){
        mydata.start=this.timeRange[0]
            mydata.end=this.timeRange[1]
        }else{
            mydata.start=new Date(new Date().getTime()-600*1000)
            mydata.end=new Date()
        }
        api.getHeadFlow({pumpNo:mydata.pumpNo,start:mydata.start.Format("yyyy-MM-dd hh:mm:ss"),end:mydata.end.Format("yyyy-MM-dd hh:mm:ss")}).then((res)=>{
            console.log(res)
            let series=[]
            for (let index = 0; index < res.data.length; index++) {
                let array=[]
                let ele=res.data[index]
                for (const key in res.data[index].data) {
                    array.push([new Date(key),ele.data[key]])
                }
                series.push({data:array,type:'line',yAxisIndex:index,name:ele.name})
            }
            let myoption={}
            myoption.tooltip={trigger:'axis'}
            myoption.legend={data:['head','flow']}
            myoption.legend.formatter=legendFormatter
            myoption.title={text:'扬程-流量'}
            myoption.xAxis={type:'time',min:mydata.start,max:mydata.end}
            myoption.yAxis=[{type: 'value',min: 'dataMin',max: 'dataMax'},{type: 'value',min: 'dataMin',max: 'dataMax'}]
            myoption.series=series
            console.log(myoption)
            var myChart = echarts.init(document.getElementById('head_flow'));
            myChart.setOption(myoption);
        })
    },
    getWorkFlow(){
        /* pumpNo=1&start=2022-10-31 07:00:00&end=2022-10-31 07:16:59 */
        let mydata={}
            mydata.pumpNo=this.pumpNo;
        if(this.timeRange.length!=0){
        mydata.start=this.timeRange[0]
            mydata.end=this.timeRange[1]
        }else{
            mydata.start=new Date(new Date().getTime()-600*1000)
            mydata.end=new Date()
        }
        api.getWorkFlow({pumpNo:mydata.pumpNo,start:mydata.start.Format("yyyy-MM-dd hh:mm:ss"),end:mydata.end.Format("yyyy-MM-dd hh:mm:ss")}).then((res)=>{
            console.log(res)
            let series=[]
            for (let index = 0; index < res.data.length; index++) {
                let array=[]
                let ele=res.data[index]
                for (const key in res.data[index].data) {
                    array.push([new Date(key),ele.data[key]])
                }
                series.push({data:array,type:'line',yAxisIndex:index,name:ele.name})
            }
            let myoption={}
            myoption.tooltip={trigger:'axis'}
            myoption.legend={data:['work','flow']}
            myoption.legend.formatter=legendFormatter
            myoption.title={text:'功率-流量'}
            myoption.xAxis={type:'time',min:mydata.start,max:mydata.end}
            myoption.yAxis=[{type: 'value',min: 'dataMin',max: 'dataMax'},{type: 'value',min: 'dataMin',max: 'dataMax'}]
            myoption.series=series
            console.log(myoption)
            var myChart = echarts.init(document.getElementById('work_flow'));
            myChart.setOption(myoption);
        })
    },
    getEfficiencyFlow(){
        /* pumpNo=1&start=2022-10-31 07:00:00&end=2022-10-31 07:16:59 */
        let mydata={}
            mydata.pumpNo=this.pumpNo;
        if(this.timeRange.length!=0){
        mydata.start=this.timeRange[0]
            mydata.end=this.timeRange[1]
        }else{
            mydata.start=new Date(new Date().getTime()-600*1000)
            mydata.end=new Date()
        }
        api.getEfficiencyFlow({pumpNo:mydata.pumpNo,start:mydata.start.Format("yyyy-MM-dd hh:mm:ss"),end:mydata.end.Format("yyyy-MM-dd hh:mm:ss")}).then((res)=>{
            console.log(res)
            let series=[]
            for (let index = 0; index < res.data.length; index++) {
                let array=[]
                let ele=res.data[index]
                for (const key in res.data[index].data) {
                    array.push([new Date(key),ele.data[key]])
                }
                series.push({data:array,type:'line',yAxisIndex:index,name:ele.name})
            }
            let myoption={}
            myoption.tooltip={trigger:'axis'}
            myoption.legend={data:['efficiency','flow']}
            myoption.legend.formatter=legendFormatter
            myoption.title={text:'效率-流量'}
            myoption.xAxis={type:'time',min:mydata.start,max:mydata.end}
            myoption.yAxis=[{type: 'value',min: 'dataMin',max: 'dataMax'},{type: 'value',min: 'dataMin',max: 'dataMax'}]
            myoption.series=series
            console.log(myoption)
            var myChart = echarts.init(document.getElementById('efficiency_flow'));
            myChart.setOption(myoption);
        })
    },
    getPredictFlow(){
        /* pumpNo=1&start=2022-10-31 07:00:00&end=2022-10-31 07:16:59 */
        let mydata={}
            mydata.pumpNo=this.pumpNo;
        if(this.timeRange.length!=0){
        mydata.start=this.timeRange[0]
            mydata.end=this.timeRange[1]
        }else{
            mydata.start=new Date(new Date().getTime()-600*1000)
            mydata.end=new Date()
        }
        api.getPredictFlow({pumpNo:mydata.pumpNo,start:mydata.start.Format("yyyy-MM-dd hh:mm:ss"),end:mydata.end.Format("yyyy-MM-dd hh:mm:ss")}).then((res)=>{
            console.log(res)
            let series=[]
            for (let index = 0; index < res.data.length; index++) {
                let array=[]
                let ele=res.data[index]
                for (const key in res.data[index].data) {
                    array.push([new Date(key),ele.data[key]])
                }
                series.push({data:array,type:'line',yAxisIndex:index,name:ele.name})
            }
            let myoption={}
            myoption.tooltip={trigger:'axis'}
            myoption.legend={data:['predict','flow']}
            myoption.legend.formatter=legendFormatter
            myoption.title={text:'预警'}
            myoption.xAxis={type:'time',min:mydata.start,max:mydata.end}
            myoption.yAxis=[{type: 'value',min: 'dataMin',max: 'dataMax'},{type: 'value',min: 'dataMin',max: 'dataMax'}]
            myoption.series=series
            console.log(myoption)
            var myChart = echarts.init(document.getElementById('predict_flow'));
            myChart.setOption(myoption);
        })
    },
  }
}
</script>

