import request from "@/api/request";

export function get(parameter) {
    return request({
      url: 'api/v5/enterprises/crrc-academy/issues',
      method: 'get',
      params:parameter
    })
}

export function userAuth(data){
    return request({
        url: 'oauth/token',
        method: 'post',
        data:data
      })
}

export function getHeadFlow(data){
    return request({
        url: 'service/head_flow',
        method: 'post',
        params:data
      })
}

export function getWorkFlow(data){
    return request({
        url: 'service/work_flow',
        method: 'post',
        params:data
      })
}

export function getEfficiencyFlow(data){
    return request({
        url: 'service/efficiency_flow',
        method: 'post',
        params:data
      })
}

export function getPredictFlow(data){
    return request({
        url: 'service/predict_flow',
        method: 'post',
        params:data
      })
}

export default { get,userAuth,getHeadFlow,getWorkFlow,getEfficiencyFlow,getPredictFlow}
